<template>
    <div @dblclick="$emit('toggle-reminder', task.id)" :class="[task.reminder?'reminder':'' , 'task']">
        <h3>
            {{ task.text }}
            <i @click="onDelete(task.id)" class="fa-solid fa-xmark fas"></i>
        </h3>
        <p>{{ task.day }}</p>
    </div>
</template>
<script>
export default{
    name: 'TaskComp',
    props :{
        task: Object
    },
    methods:{
        onDelete(id){
            // console.log(id)
            this.$emit('delete-task', id)
        }
    }


}
</script>
<style scoped>
.fas{
    color: red;
}
.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>